@import "sass/global_styles/bootstrap-classes";
@import "sass/global_styles/global";
@import "sass/global_styles/header";
@import "sass/global_styles/footer";

.contact-us {
  text-align: center;
  position: relative;
  z-index: 1;
  h1 {
    font-size: 60px;
    text-align: center;
    letter-spacing: -1.28px;
    margin: 60px 0 20px;

    @media #{$tablet} {
      font-size: 40px;
      margin: 40px 0 20px;
    }
    @media #{$mobile} {
      font-size: 28px;
      margin: 40px 0 20px;
    }
  }
}

.contact-form {
  .contact-form-content {
    background: #ffffff;
    border-radius: 40px;
    padding: 40px;

    @media #{$mobile} {
      padding: 24px 20px;
      border-radius: 24px;
    }
  }

  .form-group {
    margin-bottom: 20px;
  }

  .input-container {
    position: relative;
  }

  label:has(+ input),
  label:has(+ textarea) {
    color: #62626b;
    position: absolute;
    pointer-events: none;
    left: 24px;
    top: 12px;
    transition: 0.2s;
    font-size: 16px;
  }

  label:has(+ input:focus),
  label:has(+ input:valid),
  label:has(+ textarea:focus),
  label:has(+ textarea:valid) {
    top: -12px;
    left: 20px;
    background-color: #fff;
    padding: 0 4px;
  }

  input,
  textarea {
    background: #ffffff;
    border: 1px solid #9e9eab;
    border-radius: 11px;
    padding: 11px 24px;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    color: #0e0e11;
    width: 100%;
    display: block;
    outline: none;
    transition: 0.2s;

    &:hover,
    &:focus {
      border: 1px solid #b9c3c7;
    }

    &::placeholder {
      font-weight: normal;
    }
  }

  textarea {
    min-height: 160px;
    resize: vertical;

    @media #{$mobile} {
      min-height: 110px;
    }
  }

  button {
    font-weight: 700;
    font-size: 18px;
    line-height: 28px;
    padding: 10px;
    color: #ffffff;
    width: 100%;
    border-radius: 11px;
    box-shadow: 0px -1px 1px 0px rgba(0, 0, 0, 0.15) inset,
      0px 1px 2px 0px rgba(255, 255, 255, 0.25) inset;
    background: #5856d6;
    &:disabled {
      background: #c0c0cb;
    }

    @media #{$mobile} {
      padding: 10px;
    }
  }

  .main-btn {
    width: 100%;
    padding: 10px;
    color: #fff;
    font-size: 18px;
    font-weight: 700;
    border-radius: 11px;
    background: #5856d6;
    box-shadow: 0px -1px 1px 0px rgba(0, 0, 0, 0.15) inset,
      0px 1px 2px 0px rgba(255, 255, 255, 0.25) inset;

    &:disabled {
      background: #c0c0cb;
    }
    @media #{$mobile} {
      padding: 10px;
    }

    &.loading {
      color: rgba(#fff, 0.7);
    }

    &.loading .spinner {
      border-radius: 100%;
      border: 2px solid rgba(255, 255, 255, 0.5);
      border-left-color: #fff;
      border-top-color: #fff;
      animation: spin 0.6s infinite linear;
      opacity: 0.8;
      width: 22px;
      height: 22px;
    }

    @keyframes spin {
      to {
        transform: rotate(359deg);
      }
    }
  }

  input,
  textarea {
    &.error {
      border-color: #c8102e;
    }
  }

  span.error {
    color: #c8102e;
    font-weight: 600;
    font-size: 13px;
    line-height: 16px;
    margin: 5px 0 0;
    @media #{$mobile} {
      margin-left: 20px;
    }
  }

  span.errormsg {
    color: #c8102e;
    font-weight: 600;
    font-size: 13px;
    line-height: 16px;
    margin: 5px 0 0;
    @media #{$mobile} {
      margin-left: 20px;
    }
  }
}

.message-sent {
  display: none;
  position: relative;
  z-index: 1;
  border-radius: 40px;
  background: #fff;
  padding: 60px 0;

  @media #{$maxTab} {
    padding: 40px 20px;
    border-radius: 24px;
  }

  .content {
    max-width: 560px;
    margin: auto;
  }
  .icon {
    svg {
      @media #{$maxTab} {
        width: 60px;
        height: 60px;
      }
    }
  }
  h3 {
    margin: 30px 0 20px;
    font-size: 28px;
    @media #{$maxTab} {
      margin: 16px 0;
      font-size: 22px;
    }
  }
  p {
    color: #2d2d32;
    font-size: 20px;
    line-height: 150%;
    @media #{$maxTab} {
      font-size: 18px;
    }
  }
}
